
import api from '@/api'
import Panel from 'vux/src/components/panel/index.vue'
import Search from 'vux/src/components/search/index.vue'

import common from '@/components/common'
import Pending from '@/components/navi/gz_detail/Pending.vue'
export default {
components: {
Panel,
Search,
Pending
},
props: ['tagName'],
data () {
return {
rows: common.pageRowNo,
page: 1,
type: '1',
zcListData: [],
wxConfig: {},
footer: {
title: ''
},
searchParam: '',
doSearchFlag: false,
doSearchIndex: 1
}
},
watch: {
searchParam: function () {
if (!this.searchParam) {
this.doSearchFlag = false
}
}
},
mounted () {
this.getWxConfig()
this.loadZcListData()
},
methods: {
doSearch () {
this.doSearchFlag = true
this.doSearchIndex++
},
getWxConfig () {
let _this = this
api.post('/wx/common/getWXConfig.do', {url: document.URL}).then(function (res) {
if (res) {
_this.wxConfig = res
_this.wxConfig['debug'] = false
_this.wxConfig['jsApiList'] = ['scanQRCode']
}
})
},
scanQrcode () {

let _this = this
let wxObj = _this.$wechat
wxObj.config(_this.wxConfig)
wxObj.scanQRCode({
desc: '',
needResult: 1, // 01
scanType: ['qrCode', 'barCode'], // 
success: function (res) {
let reslt = common.scanCodeInfo(res)
if (reslt) {
_this.searchParam = reslt
_this.doSearch()
} else {
_this.$vux.alert.show({content: ''})
}
},
error: function (res) {
if (res.errMsg.indexOf('function_not_exist') > 0) {
_this.$vux.alert.show({
title: '',
content: ''
})
}
}
})
},
loadZcListData () {
let _this = this
let param = {
rows: _this.rows,
tagName: _this.tagName,
page: _this.page
}
if (_this.searchParam && _this.searchParam.length > 0) {
param['zcdm'] = _this.searchParam
}
let url = '/wx/zcgl/getMyZCList.do'
if (url) {
api.post(url, param).then(function (res) {
if (res) {

let zcList = res.zcList
_this.zcListData.push.apply(_this.zcListData, zcList)
_this.page++

if (res.countNo) {
_this.$store.dispatch('setGzCount', res.countNo.toString())
}
console.log(_this.page)// log
} else {
_this.$vux.toast.text('', 'bottom')
}
})
}
},
toZcdetail (item) {
this.$store.dispatch('zcxqShowBack')
this.$router.push({name: 'ZcxqNav', query: {zcid: item.zcid}})
}
}
}



