const tagNames = {
  hqglryz: '',
  syrz: '',
  wxz: '',
  xjz: '',
  bmzcglyz: ''
}
const pageRowNo = 10
export default {
  tagNames,
  pageRowNo,
  scanCodeInfo (result) {
    if (result) {
      let json = {}
      try {
        json = JSON.parse(result.resultStr)
        json = json.zcdm
        if (json == null || json === '') {
          json = result.resultStr
        }
      } catch (error) {
        json = result.resultStr
      }
      if (json.length > 35) {
        return false
      }
      return json
    } else {
      return false
    }
  }
}




